<template>
    <dialog-form ref="dialogFormRef" :isShow="isShow" :buttons="buttons" :inputFormProps="inputForm" :dialogProps="{ title: '', width: '500px' }" @close="emits('update:isShow', false)" />
</template>
<script setup lang="ts">
import type { Components } from "/#/components";
import { ref, reactive } from "vue";
import systemAPI from "@api/system";
import { storageStore } from "@/stores";
import { ElMessage } from "element-plus";

defineProps({
    // 
    isShow: {
        type: Boolean,
        default: false
    }
});

const emits = defineEmits(["update:isShow"]);

const dialogFormRef = ref<Components.DialogFormRef>();

// data
const storageData = storageStore();

// 
const inputForm = reactive<Components.InputForm>({
    fields: [
        {
            name: "userName",
            label: "",
            type: "label",
            value: storageData.loginUserInfo.userName
        },
        {
            name: "password",
            label: "",
            type: "input",
            props: {
                type: "password",
                maxlength: 20
            },
            rules: [{ required: true, message: "" }]
        },
        {
            name: "newPassword",
            label: "",
            type: "input",
            props: {
                type: "password",
                maxlength: 20
            },
            rules: [
                { required: true, message: "" },
                {
                    pattern: /(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[^\w\s])^([0-9A-Za-z]|[^\w\s]){8,20}$/,
                    message: "8-20",
                    trigger: ["blur"]
                }
            ]
        },
        {
            name: "repeatPassword",
            label: "",
            type: "input",
            props: {
                type: "password",
                maxlength: 20
            },
            rules: [
                { required: true, message: "" },
                {
                    validator: function (rule, value, callback) {
                        const formValue = dialogFormRef.value!.getInputValue();
                        if (formValue.newPassword && value !== formValue.newPassword) {
                            callback(new Error(""));
                        } else {
                            callback();
                        }
                    }
                }
            ]
        }
    ]
});

// 
const buttons = ref<Components.FormButton[]>([
    {
        text: ""
    },
    {
        text: "",
        props: { type: "primary" },
        click: async function (inputFormValue) {
            await dialogFormRef.value?.validate();
            await systemAPI.updateLoginPassword(inputFormValue);
            ElMessage.success("");
            emits("update:isShow", false);
        }
    }
]);
</script>
